"use client";

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Bell, Globe, Grid3X3, Loader2, Search } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { LogoutButton } from "@/components/auth/logout-button";
import { MobileSidebar } from "./mobile-sidebar";
import { getProfile } from "@/lib/api"; // ⚡ Import API call
import { useQuery } from "@tanstack/react-query";

export function DashboardTopbar() {
  // 1. Fetch User Data
  const { data: user, isLoading } = useQuery({
    queryKey: ["auth-user"],
    queryFn: getProfile,
    staleTime: 1000 * 60 * 5, // Cache for 5 mins
  });

  // Helper to get initials (e.g., "John Doe" -> "JD")
  const getInitials = (name?: string) => {
    if (!name) return "U";
    return name
      .split(" ")
      .map((n) => n[0])
      .slice(0, 2)
      .join("")
      .toUpperCase();
  };

  return (
    <header className="sticky top-0 z-40 mb-2">
      <div className="relative overflow-hidden rounded-[2rem] border border-border/40 bg-card/30 backdrop-blur-xl shadow-sm">
        <div className="flex h-16 items-center justify-between px-4 md:px-6">
          
          {/* LEFT: Mobile Toggle & Search */}
          <div className="flex items-center gap-4">
            <div className="lg:hidden">
              <MobileSidebar />
            </div>
            
            <div className="hidden md:block relative w-64 lg:w-80">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              <Input 
                placeholder="Search tenants, logs..." 
                className="h-10 rounded-full border-border/50 bg-background/50 pl-9 focus:bg-background transition-all"
              />
            </div>
          </div>

          {/* RIGHT: Actions */}
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" className="rounded-full text-muted-foreground hover:bg-background/80">
              <Globe className="h-5 w-5" />
            </Button>
            <Button variant="ghost" size="icon" className="rounded-full text-muted-foreground hover:bg-background/80">
              <Bell className="h-5 w-5" />
            </Button>
            <Button variant="ghost" size="icon" className="rounded-full text-muted-foreground hover:bg-background/80 hidden sm:flex">
              <Grid3X3 className="h-5 w-5" />
            </Button>

            {/* USER DROPDOWN */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="relative h-10 w-10 rounded-full p-0 sm:h-auto sm:w-auto sm:rounded-full sm:pl-2 sm:pr-4 sm:bg-background/50 sm:hover:bg-background/80">
                  <Avatar className="h-9 w-9 border border-border/50">
                    {/* ⚡ Fix: Use Real Avatar or Fallback to Initials */}
                    <AvatarImage src={user?.avatar_url || ""} alt={user?.name || "User"} />
                    <AvatarFallback className="bg-primary/10 text-primary font-bold">
                       {isLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : getInitials(user?.name)}
                    </AvatarFallback>
                  </Avatar>
                  
                  <div className="hidden sm:ml-3 sm:flex sm:flex-col sm:items-start text-xs">
                    {isLoading ? (
                      <>
                        <div className="h-3 w-20 animate-pulse rounded bg-muted/50 mb-1" />
                        <div className="h-2 w-16 animate-pulse rounded bg-muted/30" />
                      </>
                    ) : (
                      <>
                        <span className="font-semibold">{user?.name || "User"}</span>
                        <span className="text-muted-foreground">{user?.email || "No Email"}</span>
                      </>
                    )}
                  </div>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-56 rounded-2xl p-2">
                <DropdownMenuLabel>My Account</DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem className="rounded-xl cursor-pointer">Profile</DropdownMenuItem>
                <DropdownMenuItem className="rounded-xl cursor-pointer">Settings</DropdownMenuItem>
                <DropdownMenuSeparator />
                {/* Logout Button Component */}
                <LogoutButton mode="dropdown" />
              </DropdownMenuContent>
            </DropdownMenu>
          </div>

        </div>
      </div>
    </header>
  );
}