// frontend/components/auth/auth-guard.tsx
"use client";

import { useEffect, useState } from "react";
import { usePathname, useRouter } from "next/navigation";

import { Loader2 } from "lucide-react";
import { getProfile } from "@/lib/api";
import { useQuery } from "@tanstack/react-query";

export default function AuthGuard({ children }: { children: React.ReactNode }) {
  const router = useRouter();
  const pathname = usePathname();
  const [isChecking, setIsChecking] = useState(true);

  // 1. Initial Local Storage Check (Instant)
  useEffect(() => {
    const token = typeof window !== "undefined" ? localStorage.getItem("hive_token") : null;
    if (!token) {
        console.log("AuthGuard: No token found in storage. Redirecting.");
        redirectToLogin();
    } else {
        setIsChecking(false);
    }
  }, []);

  // 2. API Validation (Background)
  const { data: user, isError, error } = useQuery({
    queryKey: ["auth-user"],
    queryFn: getProfile,
    retry: false,
    enabled: !isChecking, // Only run if token exists
  });

  // 3. Handle API Failure
  useEffect(() => {
    if (isError) {
      console.error("AuthGuard: Token verification failed.", error);
      redirectToLogin();
    }
  }, [isError, error]);

  const redirectToLogin = () => {
    if (typeof window !== "undefined") {
        // Don't clear token immediately so we can debug, 
        // or clear it to be safe:
        localStorage.removeItem("hive_token"); 
        localStorage.removeItem("user_data");
    }
    const returnUrl = encodeURIComponent(pathname);
    router.replace(`/auth/sign-in?callbackUrl=${returnUrl}`);
  };

  if (isChecking || (user === undefined && !isError)) {
    return (
      <div className="flex h-screen w-full flex-col items-center justify-center bg-background">
        <Loader2 className="h-10 w-10 animate-spin text-primary" />
        <p className="mt-4 font-mono text-xs text-muted-foreground">VERIFYING_IDENTITY...</p>
      </div>
    );
  }

  return <>{children}</>;
}