"use client";

import React, { useEffect, useState } from "react";

import { DashboardFooter } from "./footer";
import { DashboardSidebarDesktop } from "./sidebar-desktop";
import { DashboardTopbar } from "./topbar";

const SIDEBAR_KEY = "hive_sidebar_collapsed";

export function DashboardShell({ children }: { children: React.ReactNode }) {
  const [collapsed, setCollapsed] = useState(false);

  // Hydrate sidebar state from local storage
  useEffect(() => {
    try {
      const raw = window.localStorage.getItem(SIDEBAR_KEY);
      setCollapsed(raw === "1");
    } catch { /* ignore */ }
  }, []);

  const toggleCollapsed = () => {
    setCollapsed((prev) => {
      const next = !prev;
      try {
        window.localStorage.setItem(SIDEBAR_KEY, next ? "1" : "0");
      } catch { /* ignore */ }
      return next;
    });
  };

  return (
    <div className="relative h-screen w-screen overflow-hidden bg-background">
      {/* --- Hive Background Effects --- */}
      <div className="fixed inset-0 -z-10 pointer-events-none">
         <div className="absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"></div>
         <div className="absolute left-0 right-0 top-0 -z-10 m-auto h-[310px] w-[310px] rounded-full bg-brand-primary/20 opacity-20 blur-[100px]"></div>
      </div>

      <div className="mx-auto flex h-full w-full max-w-none px-3 py-3 md:px-6 md:py-6 gap-4">
        
        {/* Left Sidebar */}
        <DashboardSidebarDesktop 
          collapsed={collapsed} 
          onToggle={toggleCollapsed} 
        />

        {/* Right Content Area */}
        <div className="flex min-w-0 flex-1 flex-col h-full">
          
          {/* 1. Sticky Topbar */}
          <DashboardTopbar />

          {/* 2. Scrollable Content */}
          <main className="flex-1 min-w-0 overflow-y-auto mt-2 pr-1 scroll-smooth no-scrollbar">
            <div className="min-h-full w-full rounded-[2rem] border border-border/40 bg-card/30 p-4 shadow-sm backdrop-blur-xl md:p-6 lg:p-8 animate-in fade-in zoom-in-95 duration-300">
              {children}
            </div>
            
            {/* 3. Footer (Scrolls with content or pinned? Pinned usually looks cleaner) */}
            <div className="py-4">
               <DashboardFooter />
            </div>
          </main>
        </div>
      </div>
    </div>
  );
}