"use client";

import { Command, PanelLeftClose, PanelLeftOpen } from "lucide-react";
import { DASHBOARD_NAV, DASHBOARD_SECONDARY } from "./nav";

import { Button } from "@/components/ui/button";
import Link from "next/link";
import { LogoutButton } from "@/components/auth/logout-button"; // See step 7
import { ThemeToggle } from "@/components/theme/theme-toggle";
import { usePathname } from "next/navigation";

export function DashboardSidebarDesktop({
  collapsed,
  onToggle,
}: {
  collapsed: boolean;
  onToggle: () => void;
}) {
  const pathname = usePathname();
  const widthClass = collapsed ? "w-[80px]" : "w-[280px]";

  return (
    <aside className={`hidden shrink-0 lg:block ${widthClass} transition-all duration-300 ease-in-out`}>
      <div className="glass-panel sticky top-6 flex h-[calc(100vh-3rem)] flex-col rounded-[2rem] border border-border/40 bg-card/30 backdrop-blur-xl p-3 shadow-xl">
        
        {/* BRAND HEADER */}
        <div className={`flex items-center ${collapsed ? "justify-center" : "justify-between"} p-2 mb-4`}>
          <Link href="/dashboard" className="flex items-center gap-3 group">
             <div className="flex h-10 w-10 items-center justify-center rounded-xl bg-primary text-primary-foreground shadow-lg shadow-primary/20 transition-transform group-hover:scale-110">
                <Command className="h-6 w-6" />
             </div>
             {!collapsed && (
               <div className="leading-none">
                 <h2 className="font-space font-bold text-lg">Hive</h2>
                 <span className="text-[10px] uppercase tracking-widest text-muted-foreground font-mono">OS v3.1</span>
               </div>
             )}
          </Link>
          
          {!collapsed && (
            <Button variant="ghost" size="icon" onClick={onToggle} className="h-8 w-8 text-muted-foreground">
              <PanelLeftClose className="h-4 w-4" />
            </Button>
          )}
        </div>

        {/* COLLAPSED EXPAND BUTTON */}
        {collapsed && (
           <Button variant="ghost" size="icon" onClick={onToggle} className="mx-auto mb-4 h-8 w-8 text-muted-foreground">
              <PanelLeftOpen className="h-4 w-4" />
           </Button>
        )}

        {/* NAV ITEMS */}
        <nav className="flex-1 space-y-1 overflow-y-auto px-1 no-scrollbar">
          {DASHBOARD_NAV.map((item) => {
            const isActive = pathname === item.href || pathname.startsWith(`${item.href}/`);
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-3 rounded-xl px-3 py-2.5 text-sm font-medium transition-all duration-200 ${
                  isActive 
                    ? "bg-primary text-primary-foreground shadow-md shadow-primary/20" 
                    : "text-muted-foreground hover:bg-muted hover:text-foreground"
                } ${collapsed ? "justify-center px-0" : ""}`}
                title={collapsed ? item.label : undefined}
              >
                <item.icon className="h-5 w-5 shrink-0" />
                {!collapsed && <span>{item.label}</span>}
              </Link>
            );
          })}
        </nav>

        {/* SECONDARY & FOOTER */}
        <div className="mt-auto space-y-2 border-t border-border/50 pt-3">
           {DASHBOARD_SECONDARY.map((item) => (
             <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-3 rounded-xl px-3 py-2 text-sm font-medium text-muted-foreground transition-colors hover:bg-muted hover:text-foreground ${
                  collapsed ? "justify-center px-0" : ""
                }`}
             >
                <item.icon className="h-4 w-4 shrink-0" />
                {!collapsed && <span>{item.label}</span>}
             </Link>
           ))}
           
           {/* LOGOUT */}
           <LogoutButton collapsed={collapsed} />

           {/* THEME TOGGLE */}
           <div className={`flex items-center rounded-xl bg-background/50 p-2 ${collapsed ? "justify-center" : "justify-between"}`}>
              {!collapsed && <span className="text-xs font-medium pl-2">Theme</span>}
              <ThemeToggle />
           </div>
        </div>

      </div>
    </aside>
  );
}