"use client";

import { Command, Menu } from "lucide-react";
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger } from "@/components/ui/sheet";

import { Button } from "@/components/ui/button";
import { DASHBOARD_NAV } from "./nav";
import Link from "next/link";
import { usePathname } from "next/navigation";

export function MobileSidebar() {
  const pathname = usePathname();

  return (
    <Sheet>
      <SheetTrigger asChild>
        <Button variant="ghost" size="icon" className="lg:hidden rounded-full">
          <Menu className="h-6 w-6" />
        </Button>
      </SheetTrigger>
      <SheetContent side="left" className="w-[300px] p-4 rounded-r-[2rem] border-border/50 bg-background/95 backdrop-blur-xl">
        <SheetHeader className="mb-6 text-left">
           <SheetTitle className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-xl bg-primary text-primary-foreground">
                  <Command className="h-6 w-6" />
              </div>
              <span className="font-space font-bold text-xl">Hive OS</span>
           </SheetTitle>
        </SheetHeader>
        
        <nav className="space-y-2">
           {DASHBOARD_NAV.map((item) => {
             const isActive = pathname === item.href;
             return (
               <Link
                 key={item.href}
                 href={item.href}
                 className={`flex items-center gap-4 rounded-xl px-4 py-3 text-sm font-medium transition-colors ${
                   isActive ? "bg-primary text-primary-foreground" : "text-muted-foreground hover:bg-muted"
                 }`}
               >
                 <item.icon className="h-5 w-5" />
                 {item.label}
               </Link>
             );
           })}
        </nav>
      </SheetContent>
    </Sheet>
  );
}